
import { 
    computed, 
    defineComponent, 
    onMounted, 
    ref 
} from 'vue';
import {
    IonGrid,
    IonRow,
    IonCol,
    IonPage,
    IonTitle,
    IonHeader,
    IonToolbar,
    IonContent,
    IonFooter,
    IonButton,
    IonItem,
    IonLabel,
    IonText
} from "@ionic/vue"
import BarcodeInput from '@/components/BarcodeInput.vue';
import HiSForm from "@/utils/PopupKeyboard"
import { FieldType } from '@/components/Forms/BaseFormElements';
import { Option } from "@/components/Forms/FieldInterface"
import { alertConfirmation, toastDanger, toastSuccess, toastWarning } from '@/utils/Alerts';
import router from '@/router';
import { TestKit } from "@/apps/HTS/composables/testKits"
import { Service } from '@/services/service';

export default defineComponent({
    components: {
        IonGrid,
        IonCol,
        IonRow,
        IonPage,
        IonTitle,
        IonHeader,
        IonToolbar,
        IonContent,
        IonFooter,
        IonButton,
        BarcodeInput,
        IonItem,
        IonLabel,
        IonText
    },
    setup() {
        const { 
            kits, 
            initKits, 
            saveKits 
        } = TestKit()
        
        const activeIndex = ref(0)

        const canFinish = ref(false)

        const hasErrors = computed(() => kits.value.some((kit: any) => 
            (!kit.lotNo && kit.expiryDate || !kit.expiryDate && kit.lotNo) ||
            kits.value.some((kit: any) => kit.expiryDateError)
        ))

        const checkExpiryDates = () => {
            kits.value.forEach((kit: any) => {
                if (kit.expiryDate && new Date(kit.expiryDate) <= new Date(Service.getSessionDate())) {
                    kit.expiryDateError = "Kit has expired!!"                    
                } else {
                    kit.expiryDateError = ''
                }
                return kit
            })
        }

        function updateLot(index: number) {
            HiSForm({
                id: 'lot',
                helpText: kits.value[index].kitName + " Lot No.",
                type: FieldType.TT_TEXT,
                defaultValue: () => kits.value[index].lotNo
            }, 
            (val: Option) => {
                const value = val?.value || ''
                if (activeIndex.value === 0) {
                    kits.value.forEach((kits: any) => {
                        if (kits.testGroup === 'profession_hiv_kit') {
                            kits.lotNo = value
                        }
                    })
                    if (kits.value[index].expiryDate) activeIndex.value = 5
                    return
                }
                if (kits.value[index].expiryDate) switchIndex()
                kits.value[index].lotNo = value
            })
        }

        function updateExpiryDate(index: number) {
            HiSForm({
                id: 'expiry',
                helpText: kits.value[index].kitName + " Expiry Date",
                type: FieldType.TT_FULL_DATE,
                defaultValue: () => kits.value[index].expiryDate
            }, 
            (val: Option) => {
                const value = val?.value || ''
                if (/all/i.test(`${kits.value[activeIndex.value].kitName}`)) {
                    kits.value.forEach((kits: any) => {
                        if (kits.testGroup === 'profession_hiv_kit') {
                            kits.expiryDate = value
                        }
                    })
                    if (kits.value[index].lotNo) activeIndex.value = 5
                    checkExpiryDates()
                    return 
                }
                if (kits.value[index].lotNo) switchIndex()
                kits.value[index].expiryDate = value
                checkExpiryDates()
            })
        }

        function switchIndex() {
            if ((activeIndex.value + 1) === kits.value.length) {
                activeIndex.value = 0
            } else {
                ++activeIndex.value
            }
        }
        
        function onScan(text: string) {
            if (/all/i.test(`${kits.value[activeIndex.value].kitName}`)) {
                kits.value.forEach((kit: any) => {
                    if (kit.testGroup === 'profession_hiv_kit') {
                        kit.lotNo = text
                    }
                })
            }
            kits.value[activeIndex.value].lotNo = text
            if (!kits.value[activeIndex.value].expiryDate) {
                updateExpiryDate(activeIndex.value)
            } else {
                switchIndex()
            }
        }
        
        async function clear() {
            if ((await alertConfirmation('Are you sure you want to clear everything?'))) {
                kits.value.forEach((kits: any) => {
                    kits.expiryDate = ''
                    kits.lotNo = ''
                    kits.expiryDateError = ''
                })
                activeIndex.value = 0
            }
        }

        async function save() {
            checkExpiryDates()
            if (hasErrors.value) {
                return toastWarning('Some kits have errors! please fix them below')
            }
            if (kits.value.every((k: any) => !(k.lotNo && k.expiryDate))) {
                if (!(await alertConfirmation("Do you want to continue without updating test kits?")))
                    return
            }
            canFinish.value = false
            try {
                await saveKits()
                toastSuccess('Testkits updated!')
                return router.back()
            } catch (e) {
                toastDanger('General error has occured!')
            }
            canFinish.value = true
        }

        onMounted(() => initKits().then(() => { checkExpiryDates(); canFinish.value = true }))

        return {
            kits,
            activeIndex,
            hasErrors,
            canFinish,
            save,
            switchIndex,
            onScan,
            updateLot,
            updateExpiryDate,
            clear
        }
    }
})
